<template>
  <div class="detail">
    <div class="ce">
      <template v-if="songDetail.backgroundCoverUrl">
        <div class="topbg" :style="'backgroundImage: url(' + songDetail.backgroundCoverUrl + ')'"></div>
      </template>
      <template v-else>
        <div class="topbg" :style="'backgroundImage: url(' + songDetail.coverImgUrl + ')'"></div>
      </template>
      <div class="pic" v-if="songDetail.backgroundCoverUrl">
        <img :src="songDetail.backgroundCoverUrl" alt />
      </div>
      <div class="pic" v-else>
        <img :src="songDetail.coverImgUrl" alt />
      </div>
      <div class="txet">
        <p>{{songDetail.name}}</p>
        <p>
          <img :src="songDetail.creator.avatarUrl" alt />
          <span>{{songDetail.creator.nickname}}</span>
        </p>
      </div>
    </div>

    <div class="tet" @click="iferer=!iferer">
      <ul>
        <li>标签：</li>
        <li v-for="(item) in songDetail.tags" :key="item">{{item}}</li>
      </ul>
      <div>
        制作人：
        <span>{{songDetail.creator.nickname}}</span>
      </div>

      <div :class="{ 'erer': iferer }">
        简介：
        {{songDetail.description}}
      </div>
    </div>
  </div>
</template> 

<script>
export default {
  props: ["songDetail"],
  data() {
    return {
      iferer: true
    };
  }
};
</script>

<style lang="less" scoped>
.detail {
  width: 100%;
}
.tet {
  padding: 15px;
  color: #666;
  background: #ffffff;
  & > div {
    font-size: 14px;
    line-height: 20px;
    // padding: 5px 0;
    &.erer {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      margin: 5px 0;
      font-size: 14px;
      margin-right: 5px;
      border-radius: 20px;
      border: 1px solid #cccccc;
      &:nth-child(1) {
        border: none;
      }
    }
  }
}
.ce {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  padding: 50px 0 30px 0;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .topbg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: blur(20px);
    z-index: -1;
    overflow: hidden;
  }
  .pic {
    width: 35%;
    img {
      width: 100%;
      vertical-align: middle;
    }
  }
  .txet {
    width: 50%;
    height: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-items: center;
    p {
      padding: 5px 0;
      &:nth-child(1) {
        font-size: 18px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      &:nth-child(2) {
        display: flex;
        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          vertical-align: middle;
          margin-right: 5px;
        }
        span {
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          font-size: 16px;
          line-height: 50px;
        }
      }
    }
  }
}
</style>