<template>
  <div class="playlist">
    <template v-if="playlist">
      <div class="top">
        <div class="go-back" @click="$router.go(-1)">
          <span class="fa fa-angle-left"></span>
          歌单
        </div>

        <SongListDetail :song-detail="playlist"></SongListDetail>
      </div>

      <ol class="songlis"> 
        <SongListItem
          v-for="(item,index) in playlist.tracks"
          :key="item.id"
          :song-item="item"
          :is-show-quality="true"
          :index="index"
          @translate-currentid="$emit('translate-currentid',$event)"
        ></SongListItem>
      </ol>
    </template>
    <HomeTitle>热门评论</HomeTitle>
    <template v-if="playListComment">
      <ul>
        <SongListcomment
          v-for="(item) in playListComment.hotComments"
          :key="item.commentId"
          :list-comment="item"
        ></SongListcomment>
      </ul>

      <ul class="somecomments">
        <li class="title" @click="someco=!someco">更多评论</li>
        <SongListcomment
          v-show="someco"
          v-for="(item) in playListComment.comments"
          :key="item.commentId"
          :list-comment="item"
        ></SongListcomment>
      </ul>
    </template>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";

import SongListItem from "@/components/SongListItem.vue";
import SongListDetail from "@/components/SongListDetail.vue";
import SongListcomment from "@/components/SongListcomment.vue";

export default {
  components: { HomeTitle, SongListItem, SongListDetail, SongListcomment },
  data: function() {
    return {
      someco: false,
      playlist: null,
      playListComment: null
      //   playlistId: null
    };
  },
  methods: {
    getPlayListDetail(id) {
      this.axios
        .get("http://musicapi.leanapp.cn/playlist/detail", {
          params: {
            id: id
          }
        })
        .then(reponse => {
          window.console.log(reponse.data);
          ({ playlist: this.playlist } = reponse.data);
        });
    },
    getPlayListComment(id) {
      this.axios
        .get("http://musicapi.leanapp.cn/comment/playlist", {
          params: {
            id: id
          }
        })
        .then(reponse => {
          window.console.log(reponse.data);
          // hotComments  comments
          this.playListComment = reponse.data;
        });
    }
  },
  created() {
    window.console.log("创建");
  },

  beforeRouteEnter(to, from, next) {
    // window.console.log(to);
    // 路由进入  当前组件实例化之前 拿数据

    next(vm => {
      vm.getPlayListDetail(to.query.id);
      vm.getPlayListComment(to.query.id);
    });
  }
};
</script>

<style >
.somecomments {
  margin-bottom: 70px;
}
li.title {
  border-left: 2px solid #d43c33;
}
.top {
  position: relative;
}

.go-back {
  position: fixed;
  top: 0;
  left: 0;
  height: 30px;
  font-size: 18px;
  color: white;
  z-index: 9;
  padding: 5px 10px;
}
.songlis {
  /* padding-left: 10px; */
}
</style>