<template>
  <div class="artist-ce">
    <div class="go-back" @click="$router.go(-1)">
      <span class="fa fa-angle-left"></span>
      返回{{this.$route.query.id}}
    </div>

    <div class="artist-top" v-if="himSongs">
      <div class="toptop">
        <div class="topbg" :style="'backgroundImage: url(' + himSongs.artist.img1v1Url + ')'"></div>
        <h3>{{himSongs.artist.name}}</h3>
      </div>
      <div class="artist-text" @click="iferer=!iferer">
        <div :class="{ 'erer': iferer }">
          <strong>歌手简介：</strong>
          {{himSongs.artist.briefDesc}}
        </div>
      </div>
    </div>
    <HomeTitle>热门歌曲</HomeTitle>

    <ol class="songlis" v-if="himSongs">
      <SongListItem
        v-for="(item,index) in himSongs.hotSongs"
        :key="item.id"
        :song-item="item"
        :is-show-quality="true"
        :index="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SongListItem>
    </ol>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";

import SongListItem from "@/components/SongListItem.vue";

export default {
  components: { HomeTitle, SongListItem },
  data() {
    return {
      id: null,
      himSongs: null,
      iferer: true
    };
  },
  methods: {
    getHimSongs() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/artists", {
          params: {
            id: this.$route.query.id
          }
        })
        .then(response => {
          window.console.log(response);
          this.himSongs = response.data;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },
  created() {
    this.getHimSongs();
  },
  updated() {
    this.id = this.$route.query.id;
  },
  watch: {
    id(value) {
      if (value) {
        this.getHimSongs();
      }
    }
  }
};
</script>

<style scope lang='less'>
.go-back {
  position: fixed;
  width: 100%;
  color: white;
  z-index: 99;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}
.artist-top {
  color: white;
  width: 100%;
  .toptop {
    width: 100%;
    height: 30vh;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }

    .topbg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // filter: blur(20px);
      background-size: cover;
      z-index: -1;
      overflow: hidden;
    }
    h3 {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 15px;
      font-size: 22px;
    }
  }

  .artist-text {
    padding: 15px;
    color: #666;
    background: #ffffff;
    & > div {
      font-size: 14px;
      line-height: 20px;
      // padding: 5px 0;
      &.erer {
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }
  }
}
</style>