<template>
  <div class="album-ce">
    <div class="go-back" @click="$router.go(-1)">
      <span class="fa fa-angle-left"></span>
      返回 {{this.$route.query.id}}
    </div>

    <div class="detail" v-if="himAlbum">
      <div class="cece">
        <div class="topbg" :style="'backgroundImage: url(' + himAlbum.album.blurPicUrl + ')'"></div>
        <div class="pic">
          <img :src="himAlbum.album.blurPicUrl" alt />
        </div>

        <div class="txet" @click="$router.push('/artist?id='+himAlbum.album.artist.id)">
          <p
            v-for="(it, index) in himAlbum.album.alias"
            :key="index"
          >{{himAlbum.album.name}}({{it}})</p>

          <p>
            歌手：{{ himAlbum.album.artist.name}}
            <span class="fa fa-chevron-right"></span>
          </p>
        </div>
      </div>

      <div class="tet" @click="iferer=!iferer">
        <div :class="{ 'erer': iferer }">
          简介：
          {{himAlbum.album.description}}
        </div>
      </div>
    </div>

    <HomeTitle>全部歌曲</HomeTitle>

    <ol class="songlis" v-if="himAlbum">
      <SongListItem
        v-for="(item,index) in himAlbum.songs"
        :key="item.id"
        :song-item="item"
        :is-show-quality="true"
        :index="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SongListItem>
    </ol>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";

import SongListItem from "@/components/SongListItem.vue";
export default {
  components: { HomeTitle, SongListItem },
  data() {
    return {
      id: null,
      himAlbum: null,
      iferer: true
    };
  },
  methods: {
    getHimAlbum() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/album", {
          params: {
            id: this.$route.query.id
          }
        })
        .then(response => {
          window.console.log(response);
          this.himAlbum = response.data;
        })
        .finally(() => {
          this.loading = false;
        });
    }
  },
  created() {
    this.getHimAlbum();
  },
  updated() {
    this.id = this.$route.query.id;
  },
  id(value) {
    if (value) {
      this.getHimSongs();
    }
  }
};
</script>

<style scope lang='less'>
.go-back {
  position: fixed;
  width: 100%;
  color: white;
  z-index: 99;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.3);
  span {
    font-size: 20px;
  }
}
.album-top {
  color: white;
  width: 100%;
  .toptop {
    width: 100%;
    height: 30vh;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
    .topbg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      filter: blur(20px);
      background-size: cover;
      z-index: -1;
      overflow: hidden;
    }
    h3 {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 15px;
      font-size: 22px;
    }
  }

  .album-text {
    padding: 15px;
    color: #666;
    background: #ffffff;
    & > div {
      font-size: 14px;
      line-height: 20px;
      // padding: 5px 0;
      &.erer {
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }
  }
}
.detail {
  width: 100%;
}
.tet {
  padding: 15px;
  color: #666;
  background: #ffffff;
  & > div {
    font-size: 14px;
    line-height: 20px;
    // padding: 5px 0;
    &.erer {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
  }
}
.cece {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  padding: 50px 0 30px 0;
  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .topbg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: blur(20px);
    z-index: -1;
    overflow: hidden;
  }
  .pic {
    width: 35%;
    img {
      width: 100%;
      vertical-align: middle;
    }
  }
  .txet {
    width: 50%;
    height: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-items: center;
    p {
      padding: 5px 0;
      &:nth-child(1) {
        font-size: 18px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      &:nth-child(2) {
        color: rgb(204, 202, 202);
        display: flex;
        line-height: 20px;
        span {
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          // font-size: 16px;
          line-height: 20px;
        }
      }
    }
  }
}
</style>